.why_container{
    height: 400px;
    width: 100%;
    .why_content{
        width: 100%;
        height: 320px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        .content{
            margin-top: 70px;
            width: 400px;
            color: white;
            text-align: center;
        }
    }
}

.why_background{
    position: absolute;
    left: 0;
    margin-top: -400px;
    height: 400px;
    width: 100%;
    background-color: #210D0F;
    z-index: -1;
}
