.Gallery_container{
    height: 400px;
    width: 100%;
    .Gallery_content{
        width: 100%;
        height: 320px;
        display: flex;
        justify-content: center;
        flex-direction: row;
        .content{
            width: 400px;
            color: white;
            text-align: center;
            img{
                width: 380px;
            }
        }
    }
}

.Gallery_background{
    position: absolute;
    left: 0;
    margin-top: -400px;
    height: 400px;
    width: 100%;
    background-color: #210D0F;
    z-index: -1;
}
