.Contact_container{
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    .Contact_content{
        width: 460px;
        height: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        .content{
            margin-top: 10px;
            width: 200px;
            color: white;
            text-align: left;
            div{
                display: flex;
                flex-direction: row;
                h2{
                    padding-left: 5px;
                }
            }
            #name{
	            font-family: GilroyEB;
                font-style: italic;
                color: #C7A460;
            }
        }
    }
    .background_container{
        position: absolute;
        width: 100%;
        height: 200px;
        max-width: 1850px;
        overflow: hidden;
        z-index: -1;
        img{
            position: absolute;
            top: -280%;
            width: 100%;
        }
    }
}
