.about_container{
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: row;
    .text_container{
        width: 50%;
        margin-right: auto;
    }
    .image_container{
        width: 50%;
        height: 100%;
        display: flex;
        align-items: center;
        img{
            width: 100%;
            max-width: 663px;
            max-height: 400px;
        }
    }
    h2{
        color: white;
        text-align: justify;
        padding-right: 50px;
    }
    h3{
        margin: 0;
        color: #210D0F;
    }
    #top{
        margin-top: 60px;
    }
    #bottom{
        margin: 20px 0px 20px 0px;
        font-size: 30px;
    }
}