nav {
    position: fixed;
    width: 100%;
    height: 40px;
    background-color: #C7A460;
}

a{
    text-decoration: none;
}

.nav_contact_container{
    position: relative;
    height: 100%;
    left: 50px;
    width: 460px;
    display: flex;
    justify-content: space-between;
    h1{
        font-weight: 400;
        margin: 0;
        line-height: 40px;
        color: #210D0F;
    }
    a{
        font-size: 13px;
        font-weight: 400;
        color: #210D0F;
        &:hover{
            font-weight: 600;
        }
    }
    div{
        display: flex;
        justify-content: left;
        flex-direction: row;
        align-items: center;
    }
    img{
        height: 20px;
        margin-right: 5px;
    }
}

.nav_hud_container{
    position: relative;
    width: 100%;
    top: 0;
    height: 70px;
    background-color: #210D0F;
    display: flex;
    justify-content: space-around;
    align-items: center;
    h2{
        color: white;
        display:inline;
        &:hover{
            font-weight: 900;
            cursor: pointer;
        }
    }
    .left h2{
        margin-right: 35px;
    }
    .right h2{
        margin-left: 35px;
    }
    img{
        position: absolute;
        left: 50%;
        -ms-transform: translate(-50%);
        transform: translate(-50%);
        top: -44px;
        width: 500px;
        transition: width 0.5s;
    }
    div{
        margin: 0px 200px 0px 200px;
    }
}